:root {
  --clr-neutral-900: hsl(207, 19%, 9%);
  --clr-neutral-100: hsl(0, 0%, 100%);
  --clr-neutral-400: hsl(142, 90%, 61%);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.card-grid-container {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(2, 1fr);

  padding: 3rem;
  padding-block: 5rem;
  /* width: min(95%, 70rem); */
  margin: 0 2rem;
}

.grid-col-span-2 {
  grid-column: span 2;
}

.grid-row-span-2 {
  grid-row: span 2;
}

.info-box {
  display: flex;
  min-width: 300px;
  border-radius: 2rem;
  color: #F5BD30;
  border: 1px solid #F5BD30;
  overflow: hidden;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}

.info-content {
  --padding: 1.5rem;
  padding: var(--padding);
  background: linear-gradient(
    hsl(0 0% 0% / 0.5),
    rgb(37, 35, 35)
  );
}

.info-content h2 {
  font-size: 60px;
  margin-bottom: 0.5rem;
}

.info-content ul {
  width: 100%;
  text-indent: 0px;
  margin-left: 20px;
  font-size: 1.7rem;
}

.info-content li {
  margin-bottom: 0.5rem;
}

.karppa-info-box {
  background-color: var(--clr-neutral-900);
  min-width: 300px;
  border: 3px solid yellow;
  overflow: hidden;
  border-radius: 2rem;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
  padding-top: 3rem;
}

.karppa-info-content {
  color: var(--clr-neutral-100);
  padding: 1.5rem;
}

.karppa-info-box img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 30%;
  height: 30%;
  object-fit: cover;
}

.karppa-info-content h2 {
  text-align: center;
  font-size: 45px;
  margin-bottom: 1rem;
  color: yellow;
}

.karppa-info-content h3 {
  text-align: center;
}

.h3-bold-yellow {
  text-align: center;
  font-size: 25px;
  margin: 1rem 0;
  color: yellow;
}

.card {
  display: flex;
  color: var(--clr-neutral-100);
  background-size: cover;
  /* max-width: 250px;
  max-height: 250px; */
  min-height: 220px;
  border-radius: 2rem;
  border: 1px solid #F5BD30;
  overflow: hidden;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
  
  /* transition: transform 500ms ease; */  
}

.card-image {
  min-height: 200px;
  object-fit: contain;  
}

.card-content {
  display: flex;
  justify-content: center;
  align-items: center;
  --padding: 1.5rem;
  padding: var(--padding);
  background: linear-gradient(
    hsl(0 0% 0% / 0.5),
    rgb(37, 35, 35)
  );
}

@media screen and (max-width: 450px) {
  .card-image {
    height: 100%;
    width: 100%;
  }

  .card {
    min-height: 100%;
    max-width: 100%;
  }
}